import React from 'react'
import gridStyle from '@components/atoms/Grid/styles.module.css'
import style from './styles.module.css'
import clsx from 'clsx'
import { Link } from '@components/atoms/Link/Component'
import CaseLarge from '@components/molecules/CaseLarge/Component'
import { ComponentBuildingBlockProjectSelection } from '@generated/graphql-request'

export default function ProjectSelection (props: ComponentBuildingBlockProjectSelection): JSX.Element {
  const cases = props.cases?.data ?? []

  return (
    <div className={clsx(gridStyle.grid, style.projectSelection)}>
      <div className={style.titleBar}>
        <h3>{props.title}</h3>
      </div>

      <div className={style.content}>
        {cases.map((item, idx): JSX.Element => {
          return (<CaseLarge key={item.id} {...item.attributes} isReversed={idx % 2 !== 0} />)
        })}
      </div>

      <div className={style.buttonBar}>
        <Link as='link' href={props.link?.path}>{props.link?.label}</Link>
      </div>
    </div>
  )
}
